import React from 'react';
import db from 'libs/db';
import { AiOutlineRight } from 'react-icons/ai';

const BottomBackNextBtn = ({
  next,
  navigation,
  params,
  nextDisabled=false,
  saveData,
  buttonText="Confirm",
  clearData=false
}) => {

  const onNextPress = () => {
    if(clearData){
      db.remove(db.model.userData)
    }
    if(nextDisabled){
      console.log("asasda",nextDisabled)
      return
    }
    if (saveData) {  
      db.get(db.model.userData).then(async (p) => {
        await db.set(db.model.userData,JSON.stringify({...p,...saveData}))
        navigation.push({pathname: next, state : params})
      });
    } else {
        console.log(next,params)
        navigation.push({pathname: next, state : params})
    }
  };

  return (
    <button
      className='solidButton'
      style={{ opacity: nextDisabled ? 0.4 : 1 }}
      onClick={() => onNextPress()}
    >
      {buttonText} <AiOutlineRight size={12}/>
    </button>
  );
};

export default BottomBackNextBtn;
