import React from 'react';
import styled from 'styled-components';
import cssVar from 'views/mobileView/styles';

const FloatingLabelInput = styled.div`
  width: 100%;
  background: transparent;
  margin-bottom: 10px;

`;

const FloatingLabelInputContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  background: transparent;

`;

const FloatingLabel = styled.label`
  padding: 0;
  margin: 0;
  border: 0;
  position: absolute;
  color: ${props =>
        props.active ? 'var(--grey) !important' : 'var(--grey) !important'};
  bottom: 0;
  transition: all 0.2s ease-in-out;
  transform-origin: left top;
  cursor: text;
  background: transparent;
  font-size: 14px;

  pointer-events: none;
  transform: ${props =>
        props.active ? 'translate3d(0, -130%, 0)' : 'translate3d(0, -20%, 0)'};
`;

const FloatingInput = styled.input`
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
    background: transparent;
    caret-color: var(--secondaryGreen);
    line-height:25px;
    border-radius: 0;
      font-size: var(--font-normal);

border-bottom: ${props =>
        props.active ? '1px solid var(--grey) !important' : '1px solid var(--light) !important'};
  &::placeholder {
    color: var(--subtle) !important;
    opacity: ${props => (props.active ? 1 : 0)};
    transition: opacity 0.2s cubic-bezier(0.6, 0.04, 0.98, 0.335);
  }
`;

export default class FloatingTextInput extends React.PureComponent {
    constructor(props) {
        super(props);
        // if (!props.id && !props.name) {
        //     throw new Error('expected id but none present');
        // }

        this.state = {
            active: props.value.toString().length > 0
        };

        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.active && this.props.value && prevProps.value !== this.props.value) {
            this.setState({ active: true });
        }
    }

    onFocus(event) {
        this.setState({ active: true });
        if (this.props.onFocus) {
            this.props.onFocus(event);
        }
    }

    onBlur(event) {
        this.setState({ active: event.target.value.length !== 0 });
        if (this.props.onBlur) {
            this.props.onBlur(event);
        }
    }

    render() {
        const { id = '', label, onBlur, onFocus, type, maxLength = '', autoCapitalize = 'none', autoFocus = false, refs, right = '', left = '', className = "bodyTextDark", height = "3.5rem", ...otherProps } = this.props;
        const { active } = this.state;

        return (
            <FloatingLabelInput style={left !== '' ? { width: "90%", marginLeft: "30px" } : {}}>
                {left !== '' ? left : null}

                <FloatingLabelInputContainer className={className} style={{ height: height }}>
                    <FloatingLabel className={className} htmlFor={label} active={active}>
                        {label}
                    </FloatingLabel>
                    <FloatingInput
                        active={active}
                        className={className}
                        autoCapitalize={autoCapitalize}
                        autoFocus={autoFocus}
                        maxLength={maxLength}
                        id={id !== '' ? id : label}
                        onBlur={this.onBlur}
                        onFocus={this.onFocus}
                        ref={refs}
                        type={type}
                        autoComplete="off"
                        {...otherProps}
                    />
                    <span style={{ position: "absolute", right: 10, bottom: 5, color: cssVar.getPropertyValue("--primary") }}>
                        {right !== '' ? right : null}
                    </span>
                </FloatingLabelInputContainer>
            </FloatingLabelInput>
        );
    }
}